.bgimg-1,
.bgimg-2,
.bgimg-3,
.bgimg-4,
.bgimg-5,
.bgimg-6 {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
}

.bgimg-1 {
    background-image: url("../image/2141259573.jpg");
}

.bgimg-2 {
    background-image: url("../image/298923743.jpg");
    background-position: right center !important;
}

.bgimg-3 {
    background-image: url("../image/1176748096.jpg");
}

.bgimg-4 {
    background-image: url("../image/1886491762.jpg");
    background-position: 30% center !important;
}

.bgimg-5 {
    background-image: url("../image/758317579.jpg");
    background-position: right center !important;
}

.bgimg-6 {
    background-image: url("../image/351281780.jpg");
}

.caption {
    display: flex;
    align-items: center;
}

h1 {
    font-size: clamp(3em,2em + 1vw,1em);
    /* line-height: 4em; */
}

header {
    align-items: center;
    display: flex;
    height: 10rem;
    width: 100%;
}

.infos {
    background: aliceblue;
    padding: 2rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    font-size: large;
    display: flex;
    flex-direction: column;
}

hr {
    height: 3rem; 
    width: 100%; 
    margin: 0;
    border: none;
}

.link__container {
    display: flex;
    align-items: center;
    margin-top: 10%;
    
    & a {
        background: #99CA3C;
        padding: 1rem 2rem;
        border-radius: 1rem;
        text-align: center;
        color: $--color-white;
    }
}

main {
    display: flex;
    justify-content: center;
}

.btn__default {
    padding: 20px;
    background-color: #069edb;
    color: $--color-white;
    text-align: center;
    margin: 20px auto;
    display: flex;
    align-items: center;
    border-radius: 10px;
    width: 160px;
    justify-content: center;
    
    & a {
        text-decoration: none;
        color: $--color-white;
    }
}

.btn__goback {
    padding: 20px;
    color: $--color-white;
    text-align: center;
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 1%;
    right: 0;
}

@mixin button($url, $width: 24px, $height: 24px) {
    margin-left: 6px;
    &::after {
        content: url($url);
        display: inline-block;
        vertical-align: middle;
        width: 24px;
        height: 24px;
        
        @if $width != 24px & $height != 24px {
            width: $width;
            height: $height;
        }
    }
}  

.button__right {
    @include button("../image/arrow_circle_right24.svg");
}

.button__left {
    @include button("../image/arrow_circle_left24.svg");
}

.button__goback {
    @include button("../image/arrow_circle_up24.svg",48px,48px);
}

h1 {
    background: $--color-white;
    padding: 4%;
}

.infos p {
    font-size: x-large;
}


/* Parallax */
#professor {
    position: relative;
    text-align: center;
    z-index: 1;
    width: 100%;
    max-width: 350px;
    // top: 150px;
}

#fundosala {
    position: absolute;
    top: 0;
    z-index: 0;
    width: 100%;
    max-width: 1230px;
}

#alunos {
    z-index: 1;
    position: relative;
    margin-top: 150px;
    width: 100%;
    max-width: 550px;
}

#textoparallax {
    top: -250px;
    position: relative;
    color: rgb(92, 3, 114);
    font-size: 5rem;
    z-index: 2;
    text-shadow: 0 1px 0 rgb(92, 3, 114), 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 0 1px 3px rgba(0,0,0,.3), 0 3px 5px rgba(0,0,0,.2), 0 5px 10px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.2), 0 20px 20px rgba(0,0,0,.15);
}

@media (max-width: 768px) {
    #textoparallax {
        font-size: 2rem;
        color: black;
        text-align: center;
        text-shadow: none;
    }
    #alunos {
        width: 300px !important;
    }
    #professor {
        margin-left: 70%;
        margin-top: -350px;
        width: 200px;
    }
}

@media (max-width: 320px) {
    #textoparallax {
        font-size: 2rem;
        color: black;
        text-align: center;
        text-shadow: none;
    }
    #alunos {
        width: 200px !important;
    }
    #professor {
        width: 100px;
        margin-top: -200px;
        margin-left: 70%;
    }
}

/* GRID GERAL */

.estrutura {
    display: grid;
    /* Minmax é para definir o mínimo que quero dela, a última eu quero 300px fixo sempre. */
    grid-template-columns: minmax(160px, 1fr) 3fr 300px;
    grid-template-areas:
    "header header header"
    "sidenav content anuncios"
    "footer footer footer"
    ;
}  

@media (max-width: 1200px) {
    .estrutura {
        grid-template-areas:
        "header header header"
        "sidenav content content"
        "sidenav anuncios anuncios"
        "footer footer footer"
        ;
    }
}

@media (max-width: 760px) {
    .estrutura {
        grid-template-columns: 100%; /* 1fr está bugando */
        grid-template-areas:
        "header"
        "sidenav"
        "content"
        "anuncios"
        "footer"
        ;
    }
}

